
.z-loading-wraper{text-align:center;height:36px;line-height:36px;width:100%;margin:20px 0
}
.z-loading-wraper>.icon-loading{color:#50C87E;font-size:24px;display:inline-block;animation:spriteSpin .8s steps(12) infinite
}
@keyframes spriteSpin{
0%{transform:rotate(0deg)
}
25%{transform:rotate(90deg)
}
50%{transform:rotate(180deg)
}
75%{transform:rotate(270deg)
}
100%{transform:rotate(360deg)
}
}


