
.main-container{min-height:inherit
}
.index-main{min-height:100%;margin-bottom:-42px
}
.index-main .index-main-body{margin:auto;padding:30px 0 102px;width:300px
}
.index-main .index-main-body .index-header{text-align:center
}
.index-main .index-main-body .sign-flow{text-align:center
}
.index-main .index-main-body .sign-flow .index-tab-navs{margin-bottom:10px;font-size:18px;text-align:center
}
.index-main .index-main-body .sign-flow .index-tab-navs .navs-slide{position:relative;display:inline-block
}
.index-main .index-main-body .sign-flow .index-tab-navs .navs-slide a{float:left;width:4em;line-height:35px;opacity:1;color:#0f88eb;-ms-filter:"alpha(Opacity=70)";transition:opacity .15s,color .15s
}
.index-main .index-main-body .sign-flow .view{float:none;margin:auto;width:300px;text-align:left;font-size:14px
}
.index-main .index-main-body .sign-flow .view .button-wrapper{margin-top:18px
}
.index-main .index-main-body .sign-flow .view .button-wrapper .z-btn{display:block;padding:0;width:100%;font-size:15px;text-align:center;text-shadow:0 1px 1px rgba(0,0,0,0.2);background:#80c3f7;background:linear-gradient(to bottom, #80c3f7, #6bbaf8);outline:0;cursor:pointer;-webkit-tap-highlight-color:rgba(0,0,0,0);background:#0f88eb;box-shadow:none;border:0;border-radius:3px;line-height:41px;color:#fff
}
.index-main .index-main-body .sign-flow .view .group-inputs{padding:1px 0;border:1px solid #d5d5d5;border-radius:3px
}
.index-main .index-main-body .sign-flow .view .group-inputs .input-wraper{position:relative;margin:0;overflow:hidden
}
.index-main .index-main-body .sign-flow .view .group-inputs .input-wraper input{padding:1em .8em;width:100%;display:inline-block;box-sizing:border-box;border:0;border-radius:0;box-shadow:none;background:#fff;line-height:19px;color:#555;margin:0
}
.index-main .index-main-body .sign-flow .view .group-inputs .input-wraper input:focus{border:0;border-radius:0;box-shadow:none;background:#fff;outline:0
}
.index-main .index-main-body .sign-flow .view .group-inputs .input-wraper+.input-wraper{border-top:1px solid #e8e8e8
}
@media (max-width:740px){
.index-main{margin-bottom:-102px
}
.sign-flow .view{float:none;margin:0 auto;width:248px
}
.sign-flow .view .button-wrapper{margin-top:22px
}
}


