
.submit-link .z-aside-content div{margin-bottom:10px
}
.submit-link .z-aside-content p{font-size:16px
}
.submit-link .z-aside-content input{margin-top:10px;padding:9px 13px;font-size:15px;min-height:26px;line-height:26px;white-space:pre-wrap;cursor:text;border:1px solid rgba(0,0,0,0.16);border-radius:4px;width:100%;resize:none;box-sizing:border-box;margin-bottom:10px;outline:0
}
.submit-link .z-aside-content input:focus{border:1px solid rgba(0,128,255,0.5)
}
.submit-link .z-aside-content .z-btn{margin:auto;display:block
}
#header-holder{height:59px
}
#header-holder .navbar{position:relative;height:58px;line-height:58px;border-bottom:1px solid transparent;background:rgba(255,255,255,0.97);z-index:500
}
#header-holder .navbar .navbar-logo-container{position:absolute;top:0;left:0;width:58px;height:58px;z-index:1
}
#header-holder .navbar .navbar-logo-container .logo{display:inline-block;width:100%;height:100%;position:absolute;top:0;left:0;line-height:58px;text-align:center;color:gray;text-decoration:none;transition:all .05s ease-in-out;font-weight:bold;font-family:"楷体"
}
#header-holder .navbar .navbar-title-container{position:absolute;width:660px;left:50%;margin-left:-330px;color:#414243;line-height:1;padding-top:10px;opacity:0;visibility:hidden
}
#header-holder .navbar .navbar-title-container>div{float:left
}
#header-holder .navbar .navbar-title-container .z-image-wraper{margin-right:12px
}
#header-holder .navbar .navbar-title-container .titles.oneline{line-height:34px
}
#header-holder .navbar .navbar-title-container .titles.oneline .main-title{font-size:16px;padding-top:3px;overflow:hidden;text-overflow:ellipsis;font-weight:700;white-space:nowrap
}
#header-holder .navbar .navbar-title-container .titles.oneline .main-title a{color:#333;text-decoration:none
}
#header-holder .navbar .navbar-title-container .functions{margin-left:32px;padding-top:3px
}
#header-holder .navbar .navbar-title-container .functions .makefriends{height:32px;padding:0 5px;min-height:0;min-width:0;line-height:30px;box-sizing:border-box;border-color:#50C87E;border:1px solid;border-radius:4px;margin:0;cursor:pointer;font-size:14px;transition:background ease-out .1s;background-color:#6861C0;color:#fff;font-weight:bold
}
#header-holder .navbar .navbar-title-container .functions .makefriends:hover{color:#6861C0;background-color:#fff
}
#header-holder .navbar .navbar-title-container.show{opacity:1;visibility:visible
}
#header-holder .navbar .navbar-title-container.anim{transition:all .2s ease-out
}
#header-holder .navbar .navbar-menu-container{position:relative;float:right;padding:0 16px 0 10px
}
#header-holder .navbar .navbar-menu-container .menu-button{color:gray
}
#header-holder .navbar .navbar-menu-container a:hover{color:#333
}
#header-holder .navbar .navbar-write-container{position:relative;float:right;padding:0 10px
}
#header-holder .navbar .navbar-write-container>a{height:54px;top:0;left:0;color:gray;text-decoration:none;display:inline-block
}
#header-holder .navbar .navbar-write-container>a:hover{color:#333
}
#header-holder .navbar .login{float:right;margin:13px 16px 0 6px;width:72px;height:32px;padding:0;min-height:0;min-width:0;line-height:30px;box-sizing:border-box;border-color:#0080ff;color:#0080ff
}
#header-holder .navbar .login:hover{background-color:rgba(0,128,255,0.06)
}
#header-holder .navbar-fixed{border-bottom-color:rgba(0,0,0,0.08);position:fixed;left:0;right:0;top:-59px;transition:all .2s ease-out
}
#header-holder .navbar-fixed-show{top:0
}
@media screen and (max-width:760px){
#header-holder .navbar .navbar-title-container{left:0;margin-left:0;padding-left:16px;width:auto;max-width:62%
}
#header-holder .navbar .navbar-title-container .z-image-wraper{display:none
}
#header-holder .navbar .navbar-title-container .functions{display:none
}
#header-holder .navbar .navbar-title-container .titles{width:calc(100% - 56px);margin-left:56px;position:relative
}
#header-holder .navbar .navbar-title-container .titles:before{content:'';height:24px;width:1px;background:rgba(0,0,0,0.16);position:absolute;left:-16px;top:8px
}
}
@media screen and (max-width:420px){
.navbar-write-container{display:none
}
}


